.wrap {
    width: 90%;
    margin: 0 auto;
    padding: 20px;

    .shoppingHead {
        text-align: center;
        font-size: 32px;
        font-weight: bold;
        margin: 20px 0;
        color: #333;
    }

    .shoppingHead2 {
        text-align: center;
        font-size: 16px;
        margin-bottom: 20px;
    }

    .table {
        width: 100%;
        border-collapse: collapse;
        margin-bottom: 20px;

        th,
        td {
            padding: 16px; /*  padding    */
            text-align: center;
            vertical-align: middle;
            border-top: 1px solid #ccc;
            border-bottom: 1px solid #ccc;
        }

        td {
            height: 50px; /*    */
            font-size: 14px;
        }

        th {
            font-size: 16px;

            &:last-of-type {
                color: #fcf9ee;
            }
        }

        select {
            width: 60px;
            padding: 5px;
            border: 1px solid #ccc;
            border-radius: 4px;
        }

        .deleteBtn {
            padding: 5px 10px;
            background-color: #fcf9ee;
            color: #1c1c1c;
            border: none;
            border-radius: 4px;
            cursor: pointer;
            text-decoration: underline;

            &:hover {
                background-color: #fcf9ee;
            }
        }
    }

    .footer {
        text-align: right;
        margin-top: 20px;
        font-size: 18px;
        font-weight: bold;

        .total {
            margin-bottom: 10px;
            color: #333;
        }

        .paymentBtn {
            padding: 10px 30px;
            background-color: #1c1c1c;
            color: #fff;
            border: none;
            border-radius: 4px;
            cursor: pointer;

            &:hover {
                background-color: #444;
            }
        }
    }

    .zero {
        text-align: center;
        margin-top: 50px;

        div {
            font-size: 1.5rem;
            margin-bottom: 20px;
        }

        a {
            display: inline-block;
            padding: 10px 20px;
            background-color: #1c1c1c;
            color: #fff;
            border-radius: 4px;
            text-decoration: none;

            &:hover {
                background-color: #444;
            }
        }
    }

    /*   */
    @media screen and (max-width: 700px) {
        .shoppingHead {
            font-size: 28px;
        }

        .shoppingHead2 {
            font-size: 14px;
        }

        .table {
            th,
            td {
                font-size: 14px;
                padding: 16px; /* 700px     */
            }

            td {
                height: 60px; /*      */
            }
        }

        .footer {
            text-align: center;

            .paymentBtn {
                width: 90%;
            }
        }
    }

    @media screen and (max-width: 500px) {
        .shoppingHead {
            font-size: 24px;
        }

        .shoppingHead2 {
            font-size: 12px;
        }

        .table {
            th,
            td {
                font-size: 10px;
                padding: 12px; /* 500px    padding */
            }

            td {
                height: 70px; /* 500px     */
            }

            .deleteBtn {
                font-size: 10px;
                padding: 4px 6px;
                background-color: #fcf9ee;
                color: #1c1c1c;
                text-decoration: underline;

                &:hover {
                    background-color: #fcf9ee;
                }
            }
        }

        .footer {
            .total {
                font-size: 14px;
            }

            .paymentBtn {
                width: 100%;
                font-size: 14px;
            }
        }

        .zero {
            div {
                font-size: 1.2rem;
            }

            a {
                width: 90%;
                font-size: 14px;
            }
        }
    }
}

@media screen and (max-width: 480px) {
    .shoppingHead {
        font-size: 24px; /*    */
    }

    .shoppingHead2 {
        font-size: 12px;
    }

    .table {
        th,
        td {
            font-size: 10px; /*    */
            padding: 6px; /* padding    */
            white-space: nowrap; /*    */
            overflow: hidden;
            text-overflow: ellipsis; /*    */
        }

        td {
            height: 40px; /*     */
        }

        .deleteBtn {
            font-size: 10px;
            padding: 4px 6px;
            background-color: #fcf9ee;
            color: #1c1c1c;

            &:hover {
                background-color: #fcf9ee;
            }
        }
    }

    .footer {
        .total {
            font-size: 12px; /*     */
        }

        .paymentBtn {
            width: 100%;
            font-size: 12px;
        }
    }

    .zero {
        div {
            font-size: 1.2rem;
        }

        a {
            width: 90%;
            font-size: 12px;
        }
    }
}
