.stepContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

.step {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    .circle {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        background-color: #ccc; /*   */
        margin-bottom: 8px;
    }

    .label {
        font-size: 14px;
        color: #666; /*   */
    }
}

.stepActive {
    .circle {
        background-color: #1c1c1c; /*   */
    }

    .label {
        color: #1c1c1c; /*    */
        font-weight: bold;
    }
}
